#data-table_length, #data-table_filter {
  margin-bottom: 40px;
  margin-top: -30px;
}

#data-table_filter {
  input {
    border: none;
    background: transparent;
    border-bottom: 1px solid rgba(7, 7, 7, .1);
    padding: 5px 5px 5px 0 !important;
  }
}

#data-table_length {
  select {
    background: transparent;
    border: 1px solid rgba(7, 7, 7, .1);
  }
}

#data-table tbody tr {
  background: transparent;
}

.table-responsive {
  margin-top: 20px;
}

#data-table_paginate {
  a {
    background-color: transparent;
    border: 1px solid rgba(7, 7, 7, .1);
  }
  span a {
    border-radius: 50%;
  }
  #data-table_previous,
  #data-table_next {
    border-radius: 50%;
  }
}

.dataTables_wrapper .dataTables_paginate .paginate_button.current, .dataTables_wrapper .dataTables_paginate .paginate_button.current:hover {
  color: #fff !important;
  background: #bd2440 !important;
  border: 1px solid #bd2440;
}

.dataTables_wrapper .dataTables_paginate .paginate_button:hover {
  color: #bd2440 !important;
  border: 1px solid #bd2440 !important;
  background: transparent;
}

.dataTables_wrapper .dataTables_paginate .paginate_button.disabled, .dataTables_wrapper .dataTables_paginate .paginate_button.disabled:hover, .dataTables_wrapper .dataTables_paginate .paginate_button.disabled:active {
  border: 1px solid #ddd !important;

}
